<script>
    export default {
        name: "child2",
        props :{
            todo : Object,
            todoArray: Array,
            idx : Number,
            id : Number,
            status : Boolean,
            completed : Boolean,
        },

        data : ()=> ({
        }),

        methods : {
            onOff : function (a,b) {
                let data = {
                    a : a ,
                    b: b
                }
                this.$emit('onOffEvent',data);
            },

            completedRow : function (a){
                let data = {
                    a : a,
                }
                this.$emit('completedRowEvent',data)
            },

            editValue : function (e) {
                let data = {
                    edit : e.target.value,
                    idx : this.idx
                }
           this.$emit('editRowEvent',data);
            },
            deleteRow : function () {
                let data = {
                    idx : this.idx
                }
                this.$emit('deleteRowEvent',data);
            },

        },
    }
</script>

