<template>
    <div id="app">
        <div id="heading">
            <h1>My to-dos</h1>
        </div>
        <div id="insert">
            <input type="text" placeholder="Create a new to do list ..." :value="text" v-on:keyup.enter="addRow">

            <button @click="deleteAll">Delete All</button>


        </div>
        <div id="table" v-for="(todo,idx) in todoArray" :key="todo.id">

            <child2 :todoArray="todoArray" :todo="todo" :idx="idx" :id="todo.id" :status="todo.status"
                    :completed="todo.completed" v-on:onOffEvent="onOffFunc" v-on:editRowEvent="changeValueRow"
                    v-on:deleteRowEvent="deleteRow" v-on:completedRowEvent="completedFunc"></child2>

        </div>
        <div id="result">
            <div>
                <p>Todo Total</p>
                <span>{{todoArray.length}}</span>
            </div>
            <div>
                <p>Todo Completed</p>
                <span>{{completedCount}}</span>
            </div>
            <div>
                <p>Need Completed </p>
                <span>{{todoArray.length - completedCount }}</span>
            </div>
        </div>


    </div>
</template>

<script>

    import child2 from "./child.vue";

    export default {
        name: "child",
        components: {
            child2
        },
        data: () => ({
            completedCount: 0,
            text: "",
            todoArray: [
                {
                    id: Math.floor(Math.random() * 100000),
                    task: "Do the dishes",
                    status: false,
                    completed: false,
                },
                {
                    id: Math.floor(Math.random() * 100000),
                    task: "Take out the trash",
                    status: false,
                    completed: false,
                },
                {
                    id: Math.floor(Math.random() * 100000),
                    task: "Finish doing laundry",
                    status: false,
                    completed: false,
                }
            ],

        }),
        methods: {
            addRow: function (e) {
                this.todoArray.push({
                    id: Math.floor(Math.random() * 100000),
                    task: e.target.value,
                    status: false,
                    completed: false,
                });
                for (let todo of this.todoArray) {
                    if (todo.status === true) {
                        todo.status = false;
                    }
                }
            },


            deleteRow: function (data) {
                return this.todoArray.splice(data.idx, 1);
            },


            deleteAll: function () {
                this.completedCount = 0
                return this.todoArray.splice(0, this.todoArray.length);
            },


            changeValueRow: function (data) {
                return this.todoArray.splice(data.idx, 1,
                    {
                        id: Math.floor(Math.random() * 100000),
                        task: data.edit,
                        status: false,
                        completed: false,
                    });
            },


            onOffFunc: function (data) {

                let open = this.todoArray.find(function (todo) {
                    return todo.id === data.a;
                });
                if (open.status === false) {
                    open.status = true
                } else {
                    open.status = false;
                }

                let close = this.todoArray.filter(function (todo) {
                    return todo.id !== data.a;
                });
                for (let closeChild of close) {
                    closeChild.status = false;
                }


            },

            completedFunc: function (data) {

                let todo = this.todoArray.find(function (todo) {
                    return todo.id === data.a
                });

                if (todo.completed === false) {
                    todo.completed = true;
                } else {
                    todo.completed = false;
                }
                let m = this.todoArray.filter(function (todo) {
                    return todo.completed === true;
                })
                this.completedCount = m.length;

                this.todoArray.sort(function (todo) {
                    let compare = 0;
                    if (todo.completed === true) {
                        compare = -1
                    } else {
                        compare = 1;
                    }
                    return compare;
                });


            },
            lost: function () {
                for (let todo of this.todoArray) {
                    console.log(todo)
                }
            },


        },

    }
</script>

